// ** Third Party Components
import {Card, CardHeader, CardTitle, CardBody, Col, CardImg, CardText, Row, Alert} from 'reactstrap';
import React from "react";
import configApi from "../../../api/config";

const UserTimeline = ({user, rewards}) => {
    const srcAsset = `${configApi.SERVER_URI}/www/templates/images/users/u_${user.user_login}/`;
    return (
        <Card>
            <CardHeader>
                <CardTitle tag='h4' className='mb-2'>
                    Награды пользователя
                </CardTitle>
            </CardHeader>
            <CardBody>
                {rewards.certificate !== '' ?
                    <Row>
                        <Col xl='6' md='6' xs={12}>
                            <Card className='plan-card'>
                                <CardImg top src={`${srcAsset}certificate/${rewards.certificate}.jpg`} alt='certificate'/>
                                <CardBody>
                                    <CardTitle tag='h4'>Сертификат участника</CardTitle>
                                    <CardText>
                                        <small className='text-muted'>Обновлено только что</small>
                                    </CardText>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col xl='6' md='6' xs={12}>
                            <Card className='plan-card'>
                                {rewards.diplom ? <>
                                    <CardImg top src={`${srcAsset}diplom/${rewards.diplom}.jpg`} alt='certificate'/>
                                    <CardBody>
                                        <CardTitle tag='h4'>Диплом</CardTitle>
                                        <CardText>
                                            <small className='text-muted'>Обновлено только что</small>
                                        </CardText>
                                    </CardBody>
                                </> : <Alert color='warning'>
                                    <h4 className='alert-heading'>Пусто!</h4>
                                    <div className='alert-body'>
                                        Пользователь не получил диплом.
                                    </div>
                                </Alert>}
                            </Card>
                        </Col>
                    </Row>
                    : <Alert color='warning'>
                        <h4 className='alert-heading'>Пусто!</h4>
                        <div className='alert-body'>
                            Пользователь еще не получил ни одной награды.
                        </div>
                    </Alert>
                }
            </CardBody>
        </Card>
    )
}

export default UserTimeline
