// ** Custom Components
import Timeline from '@components/timeline';
// ** Images
import doc from '@src/assets/images/icons/file-icons/doc.png';
// ** Third Party Components
import {Card, CardHeader, CardTitle, CardBody, Media} from 'reactstrap';
import React from "react";
import configApi from "../../../api/config";

const UserTimeline = ({user, creative}) => {
    // ** Timeline Data
    const data = [
        {
            title: 'Регистрация',
            content: 'Пользователь создал аккаунт',
            meta: user.user_reg_date
        },
        user.user_endtest_date !== '' ? {
            title: 'Тестирование',
            content: 'Пользователь прошел тестирование',
            meta: user.user_endtest_date,
            color: 'warning'
        } : null,
        user.user_uploadDate !== '' ? {
            title: 'Креативное задание',
            content: 'Пользователь отправил креативное задание',
            meta: user.user_uploadDate,
            customContent: (
                <Media className='align-items-center'>
                    <img className='mr-1' src={doc} alt='doc' height='23'/>
                    <Media body><a href={`${configApi.SERVER_URI}/www/templates/images/users/u_${user.user_login}/creative/${creative}`} target="_blank">{creative}</a></Media>
                </Media>
            )
        } : null
    ]
    return (
        <Card>
            <CardHeader>
                <CardTitle tag='h4' className='mb-2'>
                    Таймлайн пользователя
                </CardTitle>
            </CardHeader>
            <CardBody>
                <Timeline data={data}/>
            </CardBody>
        </Card>
    )
}

export default UserTimeline
