// ** React Imports
import React, {useEffect} from 'react';
import {useParams, Link} from 'react-router-dom';

import {connect} from 'react-redux';

// ** Reactstrap
import {Row, Col, Alert, Breadcrumb, BreadcrumbItem} from 'reactstrap';

// ** User View Components
import PlanCard from './PlanCard';
import UserInfoCard from './UserInfoCard';
import UserTimeline from './UserTimeline';
import InvoiceList from '../../apps/invoice/list';
import PermissionsTable from './PermissionsTable';

// ** Styles
import '@styles/react/apps/app-users.scss';
import {deleteUser, getRewards, getUserById, resetTesting, sendCreativeComment} from "../../../redux/reducers/users-reducer";
import UserRewards from "./UserRewards";

const UserView = props => {
    const {id} = useParams();
// ** Get suer on mount
    useEffect(() => {
        props.getUserById(parseInt(id));
    }, []);
    const onSubmit = () => {
        props.history.push('/users/list');
    }
    const deleteUser = (userId, userLogin, e) => {
        e.preventDefault();
        props.deleteUser(userId, userLogin, onSubmit);
    }
    const sendComment = (userId, comment, isComment) => {
        props.sendCreativeComment(userId, comment, isComment);
    }

    useEffect(() => {
        if (props.user !== null && props.user !== undefined && props.user.user_id === id) {
            props.getRewards(props.user.user_login)
        }
    }, [props.user]);
    if (props.user !== null && props.rewards !== null && props.user !== undefined && props.user.user_id === id) {
        return (
            <>
                <Breadcrumb>
                    <BreadcrumbItem>
                        <Link to='/users/list'> Все пользователи </Link>
                    </BreadcrumbItem>
                    <BreadcrumbItem active>
                        <span> {props.user.user_name} </span>
                    </BreadcrumbItem>
                </Breadcrumb>
                <div className='app-user-view'>
                    <Row>
                        <Col xl='9' lg='8' md='7'>
                            <UserInfoCard creative={props.rewards.creative} user={props.user} resetTesting={props.resetTesting} deleteUser={deleteUser}/>
                        </Col>
                        <Col xl='3' lg='4' md='5'>
                            <PlanCard creative={props.rewards.creative} user={props.user} creativeComment={props.creativeComment} sendComment={sendComment}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm='12'>
                            <InvoiceList user={props.user}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={6} className='p-0'>
                            <Col md='12'>
                                <UserTimeline creative={props.rewards.creative} user={props.user}/>
                            </Col>
                            <Col md='12'>
                                <PermissionsTable reviews={props.reviews}/>
                            </Col>
                        </Col>
                        <Col md={6} className='p-0'>
                            <Col md={12}>
                                <UserRewards rewards={props.rewards} user={props.user} />
                            </Col>
                        </Col>
                    </Row>
                </div>
            </>
        )
    } else {
        return (
            <Alert color='danger'>
                <h4 className='alert-heading'>Пользователь не найден</h4>
                <div className='alert-body'>
                    Пользователь с id: {id} не существует. Проверьте в списке пользователей: <Link to='/users/list'>Перейти</Link>
                </div>
            </Alert>
        )
    }
}

const mapStateToProps = (state) => ({
    user: state.usersList.currentUser,
    reviews: state.usersList.reviews,
    creativeComment: state.usersList.creativeComment,
    rewards: state.usersList.rewards
});

export default connect(mapStateToProps, {getUserById, deleteUser, resetTesting, sendCreativeComment, getRewards})(UserView);
