// ** React Imports
// ** Custom Components
import Avatar from '@components/avatar';
// ** Third Party Components
import {Card, CardBody, CardText, Button, Row, Col} from 'reactstrap';
import {TrendingUp, User, Phone, Globe, Database, FileText} from 'react-feather';
import React from "react";
import male from '../../../assets/images/avatars/m.svg';
import feMale from '../../../assets/images/avatars/f.svg';
import configApi from "../../../api/config";


const UserInfoCard = ({user, deleteUser, resetTesting, creative}) => {

    // ** render user img
    const renderUserImg = () => {
        if (user !== null) {
            return <img src={user.sex === 'm' ? male : feMale} alt='user-avatar' className='img-fluid rounded' height='90' width='90'/>
        } else {
            const stateNum = Math.floor(Math.random() * 6),
                states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
                color = states[stateNum]
            return (
                <Avatar
                    initials
                    color={color}
                    className='rounded'
                    content={user.user_login}
                    contentStyles={{
                        borderRadius: 0,
                        fontSize: 'calc(36px)',
                        width: '100%',
                        height: '100%'
                    }}
                    style={{
                        height: '90px',
                        width: '90px'
                    }}
                />
            )
        }
    }

    return (
        <Card>
            <CardBody>
                <Row>
                    <Col xl='8' lg='12' xs={12} className='d-flex flex-column justify-content-between border-container-lg'>
                        <div className='user-avatar-section'>
                            <div className='d-flex justify-content-start'>
                                {renderUserImg()}
                                <div className='d-flex flex-column ml-1'>
                                    <div className='user-info mb-1'>
                                        <h4 className='mb-0'>{user !== null ? user.user_name : ''}</h4>
                                        <CardText tag='span'>
                                            {user !== null ? user.user_email : ''}
                                        </CardText>
                                    </div>
                                    <Row>
                                        {user.user_endtest_date !== '' &&
                                        <Col lg={6} xs={12} className='mb-1'>
                                            <Button.Ripple color='primary' block onClick={() => resetTesting(user.user_id, user.user_login)}>
                                                Сбросить
                                            </Button.Ripple>
                                        </Col>
                                        }
                                        <Col lg={6} xs={12} className='mb-1'>
                                            <Button.Ripple color='danger' block onClick={(e) => deleteUser(user.user_id, user.user_login, e)}>
                                                Удалить
                                            </Button.Ripple>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </div>
                        <Row className='user-total-numbers'>
                            <Col lg={4} xs={12} className='mb-1'>
                                <div className='d-flex align-items-center mr-2'>
                                    <div className='color-box bg-light-primary'>
                                        <FileText className='text-primary'/>
                                    </div>
                                    <div className='ml-1'>
                                        <h5 className='mb-0'>{user.user_percent < 50 ? 'Не допущен' : user.isUpload_doc === '1' ?
                                            <a href={`${configApi.SERVER_URI}/www/templates/images/users/u_${user.user_login}/creative/${creative}`}
                                               target='_blank'>Отправлено</a> : 'Не отправлено'}</h5>
                                        <small>Креативное задание</small>
                                    </div>
                                </div>
                            </Col>
                            <Col lg={4} xs={12} className='mb-1'>
                                <div className='d-flex align-items-center'>
                                    <div className='color-box bg-light-success'>
                                        <TrendingUp className='text-success'/>
                                    </div>
                                    <div className='ml-1'>
                                        <h5 className='mb-0'>{user.user_percent ? user.user_percent + '%' : 'Тестирование не пройдено'}</h5>
                                        <small>Проценты</small>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Col>
                    <Col xl='4' lg='12' className='mt-2 mt-xl-0'>
                        <div className='user-info-wrapper'>
                            <div className='d-flex flex-wrap align-items-center my-50'>
                                <div className='user-info-title'>
                                    <Globe className='mr-1' size={14}/>
                                    <CardText tag='span' className='user-info-title font-weight-bold mb-0'>
                                        Соц. сети
                                    </CardText>
                                </div>
                                <CardText className='mb-0'>
                                    {user !== null ? user.user_social : ''}
                                </CardText>
                            </div>
                            <div className='d-flex flex-wrap align-items-center my-50'>
                                <div className='user-info-title'>
                                    <Phone className='mr-1' size={14}/>
                                    <CardText tag='span' className='user-info-title font-weight-bold mb-0'>
                                        Телефон
                                    </CardText>
                                </div>
                                <CardText className='mb-0'>{user !== null ? user.user_phone : ''}</CardText>
                            </div>
                            <div className='d-flex flex-wrap align-items-center my-50'>
                                <div className='user-info-title'>
                                    <User className='mr-1' size={14}/>
                                    <CardText tag='span' className='user-info-title font-weight-bold mb-0'>
                                        Логин
                                    </CardText>
                                </div>
                                <CardText className='mb-0'>{user !== null ? user.user_login : ''}</CardText>
                            </div>
                            <div className='d-flex flex-wrap align-items-center my-50'>
                                <div className='user-info-title'>
                                    <Database className='mr-1' size={14}/>
                                    <CardText tag='span' className='user-info-title font-weight-bold mb-0'>
                                        IP
                                    </CardText>
                                </div>
                                <CardText className='mb-0'>{user !== null ? user.user_ip : ''}</CardText>
                            </div>
                        </div>
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default UserInfoCard
