// ** Reactstrap
import {Card, CardHeader, CardBody, Badge, UncontrolledTooltip, Button, Input, Label, Row, Col, Alert} from 'reactstrap'
import configApi from "../../../api/config";
import React, {useEffect, useState} from "react";
import classnames from "classnames";

const PlanCard = ({user, sendComment, creativeComment, creative}) => {
    const [value, setValue] = useState('');
    let isComment = 0;
    useEffect(() => {
        if (creativeComment) {
            setValue(creativeComment.creative_result_comment);
            isComment = 1;
        }
    }, [])
    return (
        <Card className='plan-card border-primary'>
            <CardHeader className='d-flex justify-content-between align-items-center pt-75 pb-1'>
                <h5 className='mb-0'>Креативное задание</h5>
                <Badge id='plan-expiry-date' color='light-secondary'>
                    {user.user_uploadDate}
                </Badge>
                <UncontrolledTooltip placement='top' target='plan-expiry-date'>
                    Expiry Date
                </UncontrolledTooltip>
            </CardHeader>
            <CardBody>
                {user.user_uploadDate ? <>
                <Row>
                    <Col md={12}>
                        <div className='form-label-group mb-0'>
                            <Input
                                type='textarea'
                                name='text'
                                id='exampleText'
                                rows='3'
                                value={value}
                                placeholder='Оставьте комментарий'
                                onChange={e => setValue(e.target.value)}
                            />
                        </div>
                        <span className={classnames('textarea-counter-value float-right', {
                            'bg-danger': value.length > 100
                        })}>
                  {`${value.length}/100`}
                </span>
                    </Col>
                </Row>
                <Row className='mt-1'>
                    <Col md={6} xs={12} className='mb-1'>
                        {user.user_uploadDate ?
                            <a target="_blank" rel="noopener noreferrer" download href={`${configApi.SERVER_URI}/www/templates/images/users/u_${user.user_login}/creative/${creative}`}>
                                <Button.Ripple className='text-center' color='primary' block>
                                    Просмотреть
                                </Button.Ripple>
                            </a>
                            : user.user_percent < 50 ? 'Не допущен' : 'Не выполнено'}
                    </Col>
                    <Col md={6} xs={12} className='mb-1'>
                        <Button.Ripple className='text-center' color='success' block onClick={() => sendComment(user.user_id, value, isComment)}>
                            {creativeComment ? 'Изменить' : 'Отправить'}
                        </Button.Ripple>
                    </Col>
                </Row> </> : <Alert color='warning'>
                    <h4 className='alert-heading'>Недоступно!</h4>
                    <div className='alert-body'>
                        Данный пользователь еще не выполнил креативное задание.
                    </div>
                </Alert>}
            </CardBody>
        </Card>
    )
}

export default PlanCard
