// ** Reactstrap
import {Card, CardHeader, CardTitle, Table, CustomInput, CardText, CardBody} from 'reactstrap'
import React from "react";

const PermissionsTable = ({reviews}) => {
    const renderReview = (review) => {
        const data = [];
        for (let i = 1; i <= 5; i++) {
            data.push(
                <td>
                    <CustomInput type='checkbox' id='admin-1' label='' defaultChecked={i === parseInt(review)} disabled/>
                </td>
            )
        }
        return data;
    }
    return (
        <Card>
            <CardHeader>
                <CardTitle tag='h4'>Отзыв</CardTitle>
            </CardHeader>
            {!reviews && <CardBody>
                <CardText>Пользователь не оставил отзыв</CardText>
            </CardBody>}
            {reviews && <Table striped borderless responsive>
                <thead className='thead-light'>
                <tr>
                    <th>Категория</th>
                    <th>1</th>
                    <th>2</th>
                    <th>3</th>
                    <th>4</th>
                    <th>5</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>Информация</td>
                    {renderReview(reviews.review_information)}
                </tr>
                <tr>
                    <td>Дизайн</td>
                    {renderReview(reviews.review_design)}
                </tr>
                <tr>
                    <td>Скорость</td>
                    {renderReview(reviews.review_speed)}
                </tr>
                <tr>
                    <td>Система</td>
                    {renderReview(reviews.review_system)}
                </tr>
                <tr>
                    <td>Структура</td>
                    {renderReview(reviews.review_structure)}
                </tr>
                <tr>
                    <td>Поступление</td>
                    <td>
                        {reviews.review_university}
                    </td>
                </tr>
                </tbody>
            </Table>}
        </Card>
    )
}

export default PermissionsTable
